:root {
  --main-bg-color: #f5e3a0;
  --sec-bg-color: #f8f1d1;
  --board-background-color: #e6d3ac;
  --board-border-color: #9c7c52;
  --panel-background-color: #f8f1d1;
  --panel-border-color: #9c7c52;
  --footer-border-color: #9c7c52;
  --player-0-color: #f3f3f3;
  --player-1-color: #ee4035;
  --player-2-color: #0492c2;
  --font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

/* Scroll Bar Styles */
::-webkit-scrollbar {
  width: 10px;
  height: 100%;
}

/* Customize the track of the scroll bar */
::-webkit-scrollbar-track {
  background-color: var(--sec-bg-color);
}

/* Customize the thumb of the scroll bar */
::-webkit-scrollbar-thumb {
  background-color: var(--board-border-color);
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

/* Customize the corner of the scroll bar */
::-webkit-scrollbar-corner {
  background-color: var(--main-bg-color);
  border-top: 5px solid var(--board-border-color);
}

body {
  background-color: var(--main-bg-color);
  font-family: var(--font-family);
}

.app-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 800px;
  height: 100%;
  margin: 0 auto;
}

.panel {
  position: absolute;
  background-color: var(--panel-background-color);
  border: 8px solid var(--panel-border-color);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.75);
  z-index: 1;
}

.header {
  position: relative;
  top: 15px;
  height: 50px;
  width: 300px;
  margin-bottom: 0;
}

.header-text {
  font-size: 30px;
  font-weight: bold;
  width: 200px;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: var(--board-border-color);
}

.footer {
  height: 70px;
  width: 400px;
  margin-bottom: 0;
  background-color: var(--panel-background-color);
  border: 8px solid var(--panel-border-color);
  display: grid;
  justify-items: center;
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.75);
}

.footer button {
  width: 150px;
  height: 50px;
  background-color: var(--board-background-color);
  color: var(--board-border-color);
  font-size: 25px;
  font-weight: bold;
  border: 5px solid var(--footer-border-color);
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.footer button:hover {
  background-color: var(--board-border-color);
  color: var(--board-background-color);
  box-shadow: 0px 0px 20px 0px rgba(156, 124, 82, 0.5);
}

.gameBoard {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  padding: 20px;
  width: 500px;
  height: 500px;
  margin: 0 auto;
  background-color: var(--board-background-color);
  border: 8px solid var(--board-border-color);
  border-radius: 5%;
  -webkit-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.75);
}

.gameCircle {
  position: relative;
  width: 90px;
  height: 90px;
  margin: auto;
  border-radius: 50%;
  border: 5px solid #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: calc((100% - 90px) / 2);
}

.player_0 {
  background-color: var(--player-0-color);
}

.player_1 {
  background-color: var(--player-1-color);
}

.player_2 {
  background-color: var(--player-2-color);
}